      content:'Как передает журналист Дэвид Орнштейн, лондонский «Арсенал» может стать тем самым клубом, который «забьет последний гвоздь в крышку гроба» сделки по Ромелу Лукаку для «Челси».    Все дело в приоритетах «Интера», они готовы продать Лаутаро Мартинеса, но не хотят расставаться с бельгийским нападающим. Мартинес интересен «канонирам», но пока что наши соседи не могут договориться с «нерадзурри» о цене трансфера. Для действующих Чемпионов Италии очень важно сохранить Лукаку, поэтому они предпочтут заработать денег на трансфере Лаутаро. С другой стороны, аргентинец не нужен «Челси», ведь у Томаса Тухеля уже имеется игрок подобного плана в лице Тимо Вернера.Очевидно, что если «нерадзурри» отпустят еще и Мартинеса, тогда «синим» точно не видать Лукаку этим летом.',
